<template>
  <div class="container">
    <h1>Hello Vue and Bootstrap 4</h1>
    <button type="button" @click="loadPosts" class="button is-success">Load posts</button>

    <div v-for="(post, index) in posts" :key="index">
      <div>
        <h4><a v-bind:href="post.url">{{post.title}}</a></h4>
        <span>Created: {{post.created_date}}</span>
        <figure>{{post.abstract}}</figure>
        <p v-if="post.byline">Written by: {{post.byline}}</p>
        <img v-if="post.multimedia[0]" v-bind:src="post.multimedia[0].url"/>
      </div>
    </div>
  </div>
</template>

<script>
import axios from 'axios'

// const bootstrap = require('bootstrap')
// const axios = require('axios')
// require('bootstrap/dist/css/bootstrap.css')

export default {
  data(){
    return {
      posts: []
    }
  },
  methods: {
    loadPosts(){
      this.posts = [] // reset posts
      axios.get('https://api.nytimes.com/svc/topstories/v2/home.json?api-key=f95fd2cf96a441989dcfe7e7eb629158')
      .then(response => {
      console.log(response)
      this.posts = response.data.results
    })
    }
  }
}
</script>

<style lang="scss">

</style>



