import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/components/HomePage'

// import Admin from '@/components/Admin'
const Admin = () => import('@/components/Admin') // Import this way will split code for lazy import

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'HomePage',
      component: HomePage
    },
    {
      path: '/admin',
      name: 'Admin',
      component: Admin
    }
  ]
})



